.Main {
    position: relative;
    height: calc(100vh - 56px);
    width: 100%;
}
.Me__profile {
    display: flex;
    position: absolute;
    transform: translate(0, -56px);
    height: 100vh;
    width: 100vw;
    min-width: 320px;
    padding: 0 16px;
    background-color: rgba(21,0,47,.80);
    opacity: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    top: -200%;
    left: 0;
    transition: all 0.8s ease;
}
.Me__profile--active {
    top: 0;
    transition: all 0.8s ease;
    opacity: 1;
    z-index: 1;
}
.Me__image {
    height: 186px;
    width: 186px;
    border-radius: 100%;
    border: solid 5px rgba(9,0,20,.5);
}
.Me__full-name {
    margin-top: 28px;
    color: var(--color-soft-white);
    font-weight: bold;
    font-size: 2.8rem;
}
.Me__skills-list {
    display: flex;
    flex-direction: row;
    gap: 18px;
    margin: 40px 0 60px 0;
    user-select: none;
}
.Me__skill {
    color: var(--color-soft-white);
    font-size: 14px;
    font-weight: 500;
    background-color: var(--color-gray);
    padding: 6px 8px;
    border-radius: 4px;
}
.Me__leave-button {
    position: relative;
    cursor: pointer;
    width: 48px;
    height: 48px;
    background-color: var(--color-gray);
    border-radius: 100%;
    box-shadow: 0px 0px 8px #090014;
    transition: .3s;
}
.Me__leave-button:hover {
    transform: scale(1.15);
    background-color: var(--color-primary);
}
.Me__leave-button:focus {
    background-color: var(--color-primary);
    border: solid 1px var(--color-soft-secondary);
}
.Me__icon {
    fill: var(--color-soft-white);
    width: 24px;
    position: absolute;
    transform: translate(-50%, 50%);
}